import React from 'react';
import PropTypes from 'prop-types';
import styles from './Form.module.css';

class InputPicure extends React.Component {
    state = {
        selectedFile: null,
        src: null,
    };

    componentWillReceiveProps({ inputObj, name }) {
        this.setState({ src: inputObj[name], name });
    }

    fileSelectedHandler = (event) => {
        this.setState({
            selectedFile: event.target.files[0],
        });
    }

    fileUploadHandler = (event) => {
        event.preventDefault();
        const { selectedFile, name } = this.state;
        const reader = new FileReader();
        if (selectedFile.size > 500000) {
            alert(`Tulsgosan nagy fjlt tltttl fel! ${selectedFile.size}`);
            return;
        }
        reader.readAsDataURL(selectedFile);
        reader.onload = () => {
            this.setState({
                src: reader.result,
            });
            alert('Feltltve');
            const {
                handleChange,
            } = this.props;
            handleChange({
                target: {
                    name,
                    value: reader.result,
                },
            });
        };
        reader.onerror = function (error) {
            alert(`Hiba: ${error}`);
        };
    }

    deletePicture = () => {
        const { name } = this.state;
        const {
            handleChange,
        } = this.props;
        handleChange({
            target: {
                name,
                value: '',
            },
        });
        this.setState({
            selectedFile: null,
            src: null,
        });
    }

    render() {
        const {
            selectedForm, form, label,
        } = this.props;
        const {
            src,
        } = this.state;
        return (
            <div className={styles.formgroup} hidden={selectedForm !== form}>
                <div className={styles.label}><strong>{label}</strong></div>
                <div className={styles.formgroup}>
                    <button onClick={this.deletePicture} type="button">Trls</button>
                    <input type="file" onChange={this.fileSelectedHandler} />
                    <button onClick={this.fileUploadHandler} type="button">Feltlts</button>
                    <div className={styles.helper}>
                        {'A kp felbontsa legyen krlbell 350x400 px'}
                    </div>
                    <div className={styles.picture}>
                        <img src={src} alt="" />
                    </div>
                </div>
            </div>
        );
    }
}

InputPicure.propTypes = {
    selectedForm: PropTypes.string,
    form: PropTypes.string,
    inputObj: PropTypes.object.isRequired,
    name: PropTypes.string,
    label: PropTypes.string,
    handleChange: PropTypes.func.isRequired,
};

InputPicure.defaultProps = {
    selectedForm: '',
    form: '',
    name: '',
    label: '',
};

export default InputPicure;



