	                body: 'Ha v\xE9ge a sz\xE1mnak, akkor k\xF6sz\xF6nj\xFCk meg egym\xE1snak a t\xE1ncot, majd az \xFArnak illik visszak\xEDs\xE9rnie a h\xF6lgyet a parkett sz\xE9l\xE9re, vagy abba a t\xE1rsas\xE1gba, ahonnan felk\xE9rte.\n                    Ha egy t\xE1nc nem is \xFAgy siker\xFClt, ahogy azt tervezt\xE9tek, akkoris \u0151rizz\xE9tek meg a j\xF3kedveteket \xE9s ne b\xEDr\xE1lj\xE1tok a partnereteket, mert mindketten sz\xF3rakozni j\xF6ttetek! \n                    A b\xE1nt\xF3 megjegyz\xE9seket mindenk\xE9pp ker\xFClj\xE9tek, esetleg kritik\xE1t fogalmazhattok meg, de csak \xF3vatosan, ne vigy\xE9tek t\xFAlz\xE1sba, \xE9s ne b\xE1nt\xF3an! \n                    A dics\xE9rettel szint\xE9n m\xE9rt\xE9kkel, nehogy a m\xE1sik fej\xE9be sz\xE1lljon!\n                    Ha mindketten \xE9lvezt\xE9tek a t\xE1ncot, akkor felk\xE9rheted \xFAjra, de ne t\xFAl sokszor, mert lehet, hogy a m\xE1sik pihenni szeretne kicsit, illetve ha nincsenek komolyabb sz\xE1nd\xE9kaid, \n                    akkor nem is illik a m\xE1sikkal 2-3 sz\xE1mn\xE1l t\xF6bbet t\xE1ncolni egyhuzamban.'
